export function MyToken() {
  const history = useHistory()
  const linkTo = (url: string) => {
    history.push(url)
  }
  return (
    <div onClick={()=>{linkTo('/index')}} className="appbar-btn-pho" >PHO</div>
  )
}
export function MyLevel() {
  return (
    <img style={{width:'33px',height:'32px'}} src={require('../../assets/rfs/level.png')} alt="" />
  )
}
