import React, { useCallback, useEffect, useState } from "react";
import styled from 'styled-components'
import Icon from '../../assets/rfs/logo2@2x.png'
import { ButtonLight } from "../../components/Button";
import { BottomGrouping } from "../../components/swap/styleds";
import { useActiveWeb3React } from "../../hooks";
import { useWalletModalToggle } from "../../state/application/hooks";
import MinusIcon from '../../assets/rfs/minus.png'
import AddIcon from '../../assets/rfs/add.png'
import CloseIcon from '../../assets/rfs/close.png'

import { Dialog } from "@material-ui/core";
import './index.css'
import Addresses from '../../moonbase_address.json'
import { usePoolContract, useTokenContract } from '../../hooks/useContract';
import { MaxUint256 } from '@ethersproject/constants';
import { calculateGasMargin } from '../../utils/index';
import { TransactionResponse } from '@ethersproject/providers';
import { useTransactionAdder } from "../../state/transactions/hooks";
import { useContractAllowance } from "../../data/Allowances";
import Web3 from 'web3'
import BigNumber from "bignumber.js";
import AppBody from '../AppBody'
import { Wrapper } from '../Pool/styleds'
import TransactionConfirmationModal from '../../components/TransactionConfirmationModal'



const tokenAddress = '0x084E0952196B66098c462a253Dab9bCe81695918'

const Tab = styled.div<any>`
width: 4rem;
height: 2rem;
line-height: 2rem;
text-align: center;
border: 1px solid #F7EB8D;
opacity: 1;
border-radius: 185px;
color: ${props => props.active ? '#F7EB8D' : 'rgba(255,255,255,0.6)'};
border: ${props => props.active ? '1px #F7EB8D solid' : '1px rgba(255,255,255,0.6) solid'};    
margin-right:0.8rem;
font-size: 1rem;
cursor:pointer;
`
const ClaimBtn = styled.div<any>`
background: ${props => props.active > 0 ? 'linear-gradient(90deg, #B38631 0%, #F6DD6C 17%, #AE852E 40%, #F6DF6E 63%, #F7F19A 83%, #B28B35 100%)' : '#7C7C7C'};
color:#313031;
text-align: center;
font-size: 1rem;
border-radius: 1rem;
width: 5rem;
height: 2rem;
line-height: 2rem;
cursor: pointer;
`
const PrimaryBtn = styled(ButtonLight) <any>`
height: 1rem;

`
const StyledInput = styled.input<any>`
border:none;
width:7rem;
background: none;
color: #fff;
font-size: 1rem;
`
const MaxBtn = styled.div<any>`
background-color: #F7EB8D;
border-radius: 0.8rem;
color: #000;
cursor: pointer;
margin:0 1rem;
width: 3rem;
height: 1.8rem;
line-height: 1.8rem;
text-align: center;

`
const StyledBtn = styled.div<any>`
background-color: ${props => props.disable ? '#7C7C7C' : props.active ? '#F7EB8D' : ''};
color:${props => props.disable ? '#000' : props.active ? '#000' : '#F7EB8D'};
border:${props => props.disable ? '' : props.active ? '' : '1px #F7EB8D solid'};
border-radius: 0.6rem;
margin:0 0.4rem;
width:48%;
height:2rem;
line-height: 2rem;
text-align:center;

`




const Farm = () => {

    const { account } = useActiveWeb3React()
    // toggle wallet when disconnected
    const toggleWalletModal = useWalletModalToggle()
    const [isShowDialog, setIsShowDialog] = useState(false)
    const [tab, setTab] = useState('PHO')
    const [depositAmount, setDepositAmount] = useState('0')
    const [depositedAmount, setDepositedAmount] = useState('0')
    const [pending, setPending] = useState('0')
    const [lpAmount, setLpAmount] = useState('0')
    const [dialogTitle, setDialogTitle] = useState('质押')
    const [isAdd, setIsAdd] = useState(true)
    // modal and loading
    const [showConfirm, setShowConfirm] = useState<boolean>(false)
    const [attemptingTxn, setAttemptingTxn] = useState<boolean>(false) // clicked confirm
    const [txHash, setTxHash] = useState<string>('')
    const [pendingText, setPendingText] = useState('')

    // const [isCanClaim,setIsCanClaim]=useState(false)

    function changeTab(tab: string) {
        setTab(tab)
    }
    function handleDismiss() {
        setIsShowDialog(false)
    }


    const addTransaction = useTransactionAdder()

    const approval = useContractAllowance(tokenAddress, account, Addresses.pollAddress)

    const tokenContract = useTokenContract(tokenAddress)
    const pool = usePoolContract()
    useEffect(() => {
        const getPendingRewards = async () => {
            if (pool) {
                const p = await pool.pendingReward('0', account)
                const a = new BigNumber(Web3.utils.fromWei(p.toString(), 'ether'), undefined).toFixed(7)
                setPending(a)
            }
        };
        const t = setInterval(() => {
            getPendingRewards()
            reset()
        }, 4000)

        return (() => {
            clearInterval(t)
        })
    }, [account, pending])
    function showDialog(isAdd: boolean) {
        if (isAdd) {
            setDialogTitle('质押')
            setIsAdd(true)
        } else {
            setDialogTitle('赎回')
            setIsAdd(false)
        }
        setIsShowDialog(true)
    }
    function max() {
        if (isAdd) {
            setDepositAmount(Web3.utils.fromWei(lpAmount, 'ether').toString())
        } else {
            setDepositAmount(Web3.utils.fromWei(depositedAmount, 'ether').toString())
        }
    }
    async function getLpAmount() {
        if (tokenContract) {
            const res = await tokenContract.balanceOf(account)
            console.log(res.toString(),'getLpAmount')
            setLpAmount(res.toString())
        }
    }
    async function getDepositedAmount() {
        if (pool) {
            const res = await pool.userInfo(0, account)
            const amount = res[0].toString()
            console.log(amount,'getDepositedAmount')
            setDepositedAmount(amount)
        }
    }
    async function handleClaim() {
        setShowConfirm(true)
        setAttemptingTxn(true)
        if (pool) {
            setPendingText('Harvest')
                const estimatedGas = await pool.estimateGas.harvest('0')
                await pool.harvest('0', { gasLimit: calculateGasMargin(estimatedGas) })
                    .then((response: TransactionResponse) => {
                        console.log(response,'response')
                        setAttemptingTxn(false)
                        addTransaction(response, {
                            summary: 'harvest ',
                            approval: { tokenAddress: tokenAddress, spender: Addresses.pollAddress, }
                        })
                        setTxHash(response.hash)
                    }).catch(() => {
                        setAttemptingTxn(false)
                    })
        }
    }
    async function handleDeposit() {
        let d = Web3.utils.toWei(depositAmount, 'ether').toString()
        setShowConfirm(true)
        setAttemptingTxn(true)
        if (pool) {
            if (isAdd) {
                setPendingText('Deposit')
                const estimatedGas = await pool.estimateGas.deposit('0', d)
                await pool.deposit('0', d, { gasLimit: calculateGasMargin(estimatedGas) })
                    .then((response: TransactionResponse) => {
                        setAttemptingTxn(false)
                        addTransaction(response, {
                            summary: 'deposit ',
                            approval: { tokenAddress: tokenAddress, spender: Addresses.pollAddress, }
                        })
                        setTxHash(response.hash)
                       
                    }).catch(() => {
                        setAttemptingTxn(false)
                    })
            } else {
                setPendingText('Withdraw')
                const estimatedGas = await pool.estimateGas.withdraw('0', d)
                await pool.withdraw('0', d, { gasLimit: calculateGasMargin(estimatedGas) })
                    .then((response: TransactionResponse) => {
                        setAttemptingTxn(false)
                        addTransaction(response, {
                            summary: 'withdraw ',
                            approval: { tokenAddress: tokenAddress, spender: Addresses.pollAddress, }
                        })
                        setTxHash(response.hash)
                        
                    }).catch(() => {
                        setAttemptingTxn(false)
                    })
            }

        }
    }
    async function handleApprove() {

        if (tokenContract) {
            const estimatedGas = await tokenContract.estimateGas.approve(Addresses.pollAddress, MaxUint256).catch(() => {
                // general fallback for tokens who restrict approval amounts
                return tokenContract.estimateGas.approve(Addresses.pollAddress, MaxUint256)
            })
            console.log(estimatedGas, 'estimatedGas')
            tokenContract
                .approve(Addresses.pollAddress, MaxUint256, {
                    gasLimit: calculateGasMargin(estimatedGas)
                })
                .then((response: TransactionResponse) => {
                    addTransaction(response, {
                        summary: 'Approve ',
                        approval: { tokenAddress: tokenAddress, spender: Addresses.pollAddress, }
                    })
                })
                .catch((error: Error) => {
                    console.debug('Failed to approve token', error)
                    throw error
                })
        }

    }
    function reset() {
        console.log('reset')
        getLpAmount()
        getDepositedAmount()
    }
    useEffect(() => {
        reset()
    }, [])
    const handleDismissConfirmation = useCallback(() => {
        setShowConfirm(false)
        // if there was a tx hash, we want to clear the input

        setTxHash('')
    }, [txHash])
    useEffect(()=>{
        console.log('account change')
    },[account])


    return (
        <AppBody>
            <Wrapper>
                <TransactionConfirmationModal
                    isOpen={showConfirm}
                    attemptingTxn={attemptingTxn}
                    hash={txHash}
                    onDismiss={handleDismissConfirmation}
                    content={() => (
                        <div></div>
                    )}
                    pendingText={pendingText}
                />
                <div >
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'flex-start', width: '100%' }}>
                        <Tab onClick={() => { changeTab('PHO') }} active={tab == 'PHO'}>PHO</Tab>
                        <Tab onClick={() => { changeTab('FRS') }} active={tab == 'FRS'}>FRS</Tab>
                        <Tab onClick={() => { changeTab('FFT') }} active={tab == 'FFT'}>FFT</Tab>
                        <Tab onClick={() => { changeTab('PEC') }} active={tab == 'PEC'}>PEC</Tab>
                    </div>
                    <div style={{ background: '#2C2D32', borderRadius: '0.4rem' }}>
                        <div style={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center', width: '100%', marginTop: '1rem', padding: '0.4rem 0.8rem', borderBottom: '1px rgba(255,255,255,0.4) solid' }}>
                            <img style={{ width: '2rem', height: '2rem' }} src={Icon}></img>
                            <img style={{ width: '2rem', height: '2rem', marginLeft: '-1rem' }} src={Icon}></img>
                            <div style={{ color: '#fff' }}>FRS/FFT</div>
                        </div>
                        <div style={{ padding: '1rem' }}>
                            <div style={{ background: '#2B2B2B', border: '1px rgba(255,255,255,0.2) solid', borderRadius: '0.4rem', padding: '0.8rem' }}>
                                <div style={{ color: 'rgba(255,255,255,0.6)' }}>已赚取{tab}</div>
                                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '0.4rem' }}>
                                    <div style={{ color: '#fff', fontSize: '1rem', wordBreak: 'break-all' }}>{pending}</div>
                                    <ClaimBtn active={pending} onClick={handleClaim}>收割</ClaimBtn>
                                </div>
                            </div>
                        </div>
                        <div style={{ padding: '1rem' }}>
                            <div style={{ background: '#2B2B2B', border: '1px rgba(255,255,255,0.2) solid', borderRadius: '0.4rem', padding: '0.8rem' }}>
                                <div style={{ color: 'rgba(255,255,255,0.6)' }}>{approval ? 'FRS/FFT STAKED' : 'START FARMING'}</div>
                                <BottomGrouping>
                                    {
                                        !account ? (
                                            <PrimaryBtn onClick={toggleWalletModal}>解锁钱包</PrimaryBtn>
                                        ) : !approval ? (
                                            <PrimaryBtn onClick={handleApprove}>授权</PrimaryBtn>

                                        ) :
                                            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                                <div style={{ color: '#fff', fontSize: '1rem', wordBreak: 'break-all' }}>{Web3.utils.fromWei(depositedAmount, 'ether').toString()}</div>
                                                <div style={{ display: 'flex' }}>
                                                    <img onClick={() => { showDialog(false) }} style={{ width: '1.6rem', height: '1.6rem', marginRight: '0.6rem', cursor: 'pointer' }} src={MinusIcon}></img>
                                                    <img onClick={() => { showDialog(true) }} style={{ width: '1.6rem', height: '1.6rem', cursor: 'pointer' }} src={AddIcon}></img>
                                                </div>
                                            </div>
                                    }
                                </BottomGrouping>
                            </div>
                        </div>
                        <div style={{ padding: '1rem' }}>
                            <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '0.5rem' }}>
                                <div style={{ color: '#fff', fontSize: '0.8rem', wordBreak: 'break-all' }}>APR</div>
                                <div style={{ color: '#fff', fontSize: '0.8rem', wordBreak: 'break-all' }}>300%</div>
                            </div>
                            <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '0.5rem' }}>
                                <div style={{ color: '#fff', fontSize: '0.8rem', wordBreak: 'break-all' }}>倍数</div>
                                <div style={{ color: '#fff', fontSize: '0.8rem', wordBreak: 'break-all' }}>30X</div>
                            </div>
                            <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '0.5rem' }}>
                                <div style={{ color: '#fff', fontSize: '0.8rem', wordBreak: 'break-all' }}>流动性</div>
                                <div style={{ color: '#fff', fontSize: '0.8rem', wordBreak: 'break-all' }}>$23,232</div>
                            </div>
                            <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '0.5rem' }}>
                                <div style={{ color: '#fff', fontSize: '0.8rem', wordBreak: 'break-all' }}>质押</div>
                                <div style={{ color: '#fff', fontSize: '0.8rem', wordBreak: 'break-all' }}>FRS/FFT</div>
                            </div>
                        </div>
                    </div>
                    <Dialog onClose={handleDismiss} open={isShowDialog}>
                        <div style={{ padding: '1rem', background: '#2C2D32' }}>
                            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                <div style={{ color: '#fff', fontSize: '1rem' }}>{dialogTitle}LP代币</div>
                                <img onClick={handleDismiss} style={{ width: '0.8rem', height: '0.8rem' }} src={CloseIcon}></img>
                            </div>
                            <div style={{ padding: '1rem' }}>
                                <div style={{ background: '#2B2B2B', border: '1px rgba(255,255,255,0.2) solid', borderRadius: '0.4rem', padding: '0.8rem' }}>
                                    <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                                        <div style={{ color: '#fff', fontSize: '1rem' }}>{dialogTitle}</div>
                                        <div style={{ color: '#fff', fontSize: '1rem' }}>余额:{isAdd ? new BigNumber(Web3.utils.fromWei(lpAmount, 'ether')).toFixed(7) : new BigNumber(Web3.utils.fromWei(depositedAmount, 'ether')).toFixed(7)}</div>
                                    </div>
                                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '0.4rem' }}>
                                        <div style={{ display: 'flex' }}>
                                            <StyledInput value={depositAmount} onChange={(v: any) => { setDepositAmount(v.target.value) }}></StyledInput>
                                            <MaxBtn onClick={max}>Max</MaxBtn>
                                        </div>

                                        <div style={{ color: '#fff', fontSize: '1rem' }}>FRS-FFT</div>

                                    </div>
                                </div>
                            </div>
                            <div style={{ color: '#fff', fontSize: '1rem' }}>Get FRS-FFT</div>
                            <div style={{ display: 'flex', justifyContent: 'space-between', marginTop: '2rem', alignItems: 'center' }}>
                                <StyledBtn onClick={() => { setIsShowDialog(false) }}>取消</StyledBtn>
                                <StyledBtn onClick={handleDeposit}>确认</StyledBtn>
                            </div>
                        </div>
                    </Dialog>
                </div>
            </Wrapper>
        </AppBody>

    )
}

export default Farm